import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Observable } from 'rxjs';
import { Thread, Collections, ThreadResponse } from '../../app/app.models';
import { AngularFirestore } from 'angularfire2/firestore';
import { AuthProvider } from '../../providers/auth/auth';

@IonicPage({
  segment: 'hilo/:id'
})
@Component({
  selector: 'page-hilo-detail',
  templateUrl: 'hilo-detail.html',
})
export class HiloDetailPage {

  private id: string = this.navParams.get('id')
  public thread$: Observable<Thread>
  public responses$: Observable<ThreadResponse[]>

  public tempR: string = ''
  public lc: boolean = false

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private afs: AngularFirestore,
    private auth: AuthProvider
  ) {
  }

  ionViewDidLoad() {

    this.thread$ = this.afs.collection(Collections.THREAD).doc<Thread>(this.id).valueChanges()
    this.responses$ = this.afs.collection<ThreadResponse>(Collections.THREAD_RESPONSE, ref => ref.where('thread', '==', this.id)).valueChanges()

  }

  async addRespuesta(text: string) {

    this.lc = true
    const id = this.afs.createId()

    await this.afs.collection(Collections.THREAD_RESPONSE).doc<ThreadResponse>(id).set({
      id,
      text,
      user: this.auth.user,
      user_id: this.auth.user.uid,
      thread: this.id,
      date: new Date().toISOString()
    })

    this.tempR = ''

  }

}



